.container,
.wrapper {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: scroll
}
.bgEle1,
.bgEle1_1 {
    position: fixed;
    height: fit-content;
    top: -2%;
    right: 0;
    width: 40%;
    z-index: -1;
}
.bgEle1 > img {
    width: 100%;
    height: auto;
    object-fit: contain;
    display: block;
    transform: rotateY(180deg);
}
.bgEle1_1 {
    top: -2%;
    left: -4%;
}
.bgEle1_1 > img {
    width: 100%;
    height: auto;
    object-fit: contain;
    display: block;
}
.bgEle2 {
    position: fixed;
    height: fit-content;
    width: 40%;
    bottom: -5%;
    right: -10%;
}
.bgEle2 > img {
    width: 100%;
    height: auto;
    object-fit: contain;
    display: block;
}
.wrapper {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 32px 56px 150px 56px;
    gap: 12px;
}
.header {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.title {
    font: 600 42px Prompt;
    overflow: hidden;
}
.board_ct {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 0;
    gap: 56px;
}
.right,
.left {
    flex: 1;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 6px;
}
